.contact-form {
    @apply border border-richblack-600 text-richblack-300 rounded-xl p-7 lg:p-14 flex gap-3 flex-col;
}

.contact-form-heading {
    @apply text-4xl leading-10 font-semibold text-richblack-5;
}

.contact-form-para {
    @apply mx-auto;
}

.contact-form-form {
    @apply mt-7;
}
